
header {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    z-index: 100;
    .header__movie-nav {
        align-items: center;
        position: fixed;
        top: 15%;
        left: 0%;
        z-index: 30;
        width: 100%;
    }
    .header__movie{
        .navbar-nav{
            border-bottom: 2px solid rgba(255, 255, 255, 0.3);
            .nav-item{
                padding: 0 15px;
                display: inline-block;
                position: relative;
                .nav-link{
                    &::after{
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        left: 0;
                        display: block;
                        height: 3px;
                        content: '';
                        background-image: linear-gradient(to right, #fbbd61, #ec7532);
                        box-shadow: 0 0 20px 0 #fbbd61;
                        opacity: 0;
                        transform: translateX(-100px);
                        transition: all 0.5s ease-in-out;
                    }
                    &:hover{
                        opacity: 1;
                        transform: translateX(0px);
                    }
                }
            }
            .active{
                &::after{
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    display: block;
                    height: 3px;
                    content: '';
                    background-image: linear-gradient(to right, #fbbd61, #ec7532);
                    box-shadow: 0 0 20px 0 #fbbd61;
                    opacity: 1;
                    transform: translateX(0px);
                }
            }
        }
    }
}